@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	margin: 0;
	padding: 0;
}

a {
	color: #2980b9;
}

body {
	font-family: 'Roboto', sans-serif;
}

.p-t-b-25 {
	padding: 40px 0px 25px 0px;
}

.title-heading {
	color: #fff;
	margin-top: 20px;
	background: #667db6;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.stats-title {
	font-size: 15px;
	font-weight: 600;
	color: #fff;
}

.main-card {
	background: #8E2DE2;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #4A00E0, #8E2DE2);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	border-radius: 10px;
	padding: 10px 0px;
	box-shadow: 5px 5px 25px -2px rgba(0, 0, 0, 0.25);
}

.main-card p {
	margin-bottom: 0;
}

.stats-p {
	padding-top: 20px;
}

.death {
	background: #ED213A;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #93291E, #ED213A);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.today {
	background: #e53935;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to top, #e35d5b, #e53935);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.active {
	background: #134E5E;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to top, #71B280, #134E5E);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.recovered {
	background: #667db6;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.todays-death {
	background: #FF512F;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to top, #DD2476, #FF512F);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.today-affected {
	background: #AA076B;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #61045F, #AA076B);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.stats-title p:nth-child(2) {
	font-size: 35px;
}

.stats {
	width: 100px;
	height: 100px;
	background: #8e44ad;
	display: block;
	border-radius: 50%;
	margin: 0 auto;
}

.stats p {
	color: #fff;
	font-size: 50px;
	position: relative;
	top: 12px;
	font-weight: 500;
}

.header {
	color: #fff;
	background: #4b6cb7;
	/* fallback for old browsers */
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #182848, #4b6cb7);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.stats-pp {
	margin-top: 100px;
	font-size: 18px;
}

.whole-world ul li {
	margin-bottom: 15px;
}

.qqq{
    background: #000000;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #434343, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.whole-world ul li span {
	padding: 3px;
}

.whole-world ul {
	margin-left: 20px;
}

.footer {
	padding-top: 50px;
}

.footer-credites p {
	color: #7f8c8d;
}

.save-header {
    margin-bottom: 30px;
}

.save-header h4{
    color: #1e3c72;
    font-weight: 700px;
}

.version {
    font-size: 10px;
    color: darkgray;
}


@media only screen and (max-width: 600px) {
	.stats {
		margin-bottom: 50px;
	}
	.stats-title {
		font-size: 18px;
		font-weight: 400;
		color: #fff;
	}
	.stats-pp {
		margin-top: 10px;
	}
	.main-card {
		padding: 20px;
		margin-bottom: 20px;
	}
	.title-heading h3 {
		font-size: 1.5rem;
    }
    .youtube-video {
        padding: 20px 20px;
    }
    a.navbar-brand.text-white {
		padding: 15px 0;
		margin: 0;
	}
	.navbar{
		padding: 0;
	}
}
